<script>
import {
  BFormFile,
  BButton,
  BForm,
  BFormGroup,
  BFormInput,
  BFormSelect,
  BRow,
  BCol,
  BAlert,
  BCard,
  BCardText,
  BMedia,
  BMediaAside,
  BMediaBody,
  BLink,
  BImg,
} from 'bootstrap-vue'
import { required, email } from '@validations'
import Ripple from 'vue-ripple-directive'
import { useInputImageRenderer } from '@core/comp-functions/forms/form-utils'
import { ref } from 'vue'
import axios from '@/libs/axios'
import ToastificationContent from '@core/components/toastification/ToastificationContent'
import { ValidationProvider, ValidationObserver } from 'vee-validate'

export default {
  name: 'UserProfile',
  components: {
    BButton,
    BForm,
    BImg,
    BFormFile,
    BFormGroup,
    BFormInput,
    BFormSelect,
    BRow,
    BCol,
    BAlert,
    BCard,
    BCardText,
    BMedia,
    BMediaAside,
    BMediaBody,
    BLink,
    ValidationProvider,
    ValidationObserver,
  },
  directives: {
    Ripple,
  },
  props: {
    generalData: {
      type: Object,
      default: () => {
      },
    },
  },
  data() {
    return {
      userImage: 'sa',
      userName: 'sa',
      userAccount: 'sa',
      userMobile: 'sa',
      userEmail: 'sa',
      userSelectedLanguage: 'a',
      token: '',
      options: null,
      // optionsLocal: JSON.parse(JSON.stringify(this.generalData)),
      // profileFile: null,
      required,
      email,
    }
  },
  mounted() {

    this.token = sessionStorage.getItem('token') ? sessionStorage.getItem('token') : localStorage.getItem('token')
    axios
      .get('https://sm.oop.ir/api/v1/user/profile', {
        headers: {
          Authorization: `Bearer ${this.token}`,
        },
      })
      .then(response => {
        this.userImage = response.data.profile.avatar
        this.userName = response.data.profile.name
        this.userAccount = response.data.profile.account_code
        this.userMobile = response.data.profile.mobile
        this.userEmail = response.data.profile.real_email
        this.userSelectedLanguage = response.data.profile.settings.locale
        this.options = response.data.profile.settings.locale_list
      })
      .catch(error => {
      })
      .finally(() => {})
  },
  methods: {
    resetForm() {
      this.optionsLocal = JSON.parse(JSON.stringify(this.generalData))
    },
    profileEdit() {
      const profile = new FormData()
      profile.append('name', this.userName)
      profile.append('email', this.userEmail)
      profile.append('locale', this.userSelectedLanguage)
      axios
        .post('https://sm.oop.ir/api/v1/auth/send-otp', profile,{
          headers: {
            Authorization: `Bearer ${this.token}`,
          },
        })
        .then(response => {
          this.$refs.loginValidation.validate().then(success => {
            if (success) {
              this.$toast({
                component: ToastificationContent,
                props: {
                  title: 'پروفایل شما با موفقیت به روزرسانی شد',
                  icon: 'EditIcon',
                  variant: 'success',
                },
              })
            }
          })
        })
        .catch(error => {
          this.$toast({
            component: ToastificationContent,
            props: {
              title: error.response.data.message,
              icon: 'EditIcon',
              variant: 'error',
            },
          })
        })
        .finally(() => {
        },
          // this.loading = false
        )
    },
  },
}
</script>

